@import 'styles/partials/variables';



.navigation {
    position: relative;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    gap: $page-padding;
    padding: $page-padding 16px 8px $page-padding;
    background-color: $mountain-blue;
    // position: fixed;

    &::before {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        // Define a background image with a inset as overlay shadow.
        background: url("../../assets/images/background-mountains1.png");
        background-size: cover;
        background-position: top;
        z-index: -1;
    }

    .logo {
        font-family: 'Oswald', sans-serif;
        display: flex;
        align-items: center;
        line-height: 1.2em;
        color: $sunrise-yellow;
        font-size: clamp(48px, $scale-property, 64px);
        font-weight: 700;
        svg {
            fill: $sunrise-yellow;
        }
    }

    ul {
        padding-left: 2rem;
        padding-right: 2rem;
        display: flex;
        flex-flow: row nowrap;
        flex-grow: 1;
        justify-content: space-around;

        li {
            list-style: none;
            display: flex;
            flex-flow: row wrap;
            flex-grow: 1;
            justify-content: center;
            
        }
        @media  (max-width: 768px) {
            li:not(:last-child):not(:first-child) {
                display: none;
            }
        }
        @media  (max-width: 540px) {
            li:not(:last-child) {
                display: none;
            }
        }

    }

}