@import 'styles/partials/variables';

.linkButton {
    display: flex;
    flex-flow: row nowrap;
    width: calc(100% - 2rem);
    align-self: center;
    border-radius: 0.5rem;
    transition: all 0.3s ease-out;
    align-items: center;
    justify-content: space-between;
    color: $misty-white;
    padding-top: 0.2em;
    padding-bottom: 0.2em;

    &.big {
        font-size: clamp(24px, $scale-property, 32px);
    }
    &.small {
        max-width: max-content;
    }

    &:hover {
        background-color: $mountain-shade-50;
        scale: 1.05;
        width: calc(100% - 2rem - 5%);
        transition: all 0.1s ease-out; 
    }
    &.highlighted {
        background-color: $sunrise-yellow-75;
        color: $mountain-blue;
        font-weight: 400;
    }

    span {
        width: 2em;
    }
    svg {
        width: 2em;
        font-size: 1em;
    }
}

.LinkText{
}

.actionButton {
    justify-content: center;
    align-items: center;
    height: clamp(64px, $scale-property, 80px);
    background-color: $sunrise-yellow;
    color: $mountain-blue;
    border-radius: 50px;
    padding: 2rem;
    transition: 0.3s ease-out;

    &:hover, &:active {
        background-color: $mountain-blue;
        text-decoration: none;
        * {
            transition: 0.3s ease-out;
            color: $sunrise-yellow;
        }
    }
}

.attentionGrabbing {
    --rotation: 0.7deg;
    animation: attentionGrabbing 4s infinite;
}

@keyframes attentionGrabbing {
    0% {
 
    }
    90% {
        transform: rotate(0deg) 
    }
    92% {
        transform: rotate(var(--rotation))
    }
    94% {
        transform: rotate(calc(var(--rotation) * -1)) 
    }
    96% {
        transform: rotate(var(--rotation)) 
    }
    98% {
        transform: rotate(calc(var(--rotation) * -1)) 
    }
    100% {
        transform: rotate(0deg);
    }
}
